import React from "react";
import { Grid, Box, Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(({ palette, ...theme }) => ({
  media: {
    width: "80%",
  },

  p: {
    fontFamily: "Roboto",
    fontStyle: "normal",
    fontWeight: "400",
    fontSize: "1em",
    lineHeight: "30px",
    textAlign: "justify",
  },

  souTitle: {
    fontFamily: "Roboto",
    fontStyle: "normal",
    fontWeight: "500",
    fontSize: "1em",
    lineHeight: "30px",
    textAlign: "center",
  },

  title: {
    fontFamily: "LEMON MILK",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "1.5em",
    color: "#384C5A",
  },

  titleText: {
    fontFamily: "LEMON MILK light",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "1em",
    color: "#384C5A",
  },

  box: {
    marginBottom: "4em",
  },
}));

const FirstSection = () => {
  const classes = useStyles();

  return (
    <section className="section">
      <Container maxWidth="lg">
        <Box textAlign="center">
          <h3 className="font-bold text-28 text-center text-second mt-5 mb-10">
            Coaching
          </h3>
          <p className={classes.souTitle}>
            Pour le stage de coaching, merci d'appeler le centre IKDO sur le
            numro 07 86 99 59 40.
          </p>
        </Box>
      </Container>

      <Container maxWidth="lg" className="container-formations">
        <Box pt={8} pb={10}>
          <Grid
            container
            spacing={2}
            textAlign="center"
            justify="center"
            alignItems="center"
          >
            <Grid item xs={12} md={4}>
              <h3 className={classes.title}> Le coaching: </h3>

              <h4 className={classes.titleText}> qu'est ce que c'est? </h4>
              <img
                alt=""
                className={classes.media}
                src="/assets/images/coaching/coach.png"
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <Box display="flex" height="100%">
                <Box my="auto" className={classes.box} mt={4}>
                  <p className={classes.p}>
                    Le coaching vous apporte une vritable rponse  vos
                    questions d'ordre professionnel ou priv pour rvler le
                    meilleur de vous-mme, ceci vous permet galement de fixer
                    de nouveaux objectifs, une direction  suivre.
                  </p>
                  <br></br>

                  <p className={classes.p}>
                    Pour cette semaine pas comme les autres, en immrsion dans
                    la nature, je vous accueille  Cormont, au cur d'un petit
                    village face  l'Eglise, au cur de soi et en connexion avec
                    cette magnifique nature authentique. A Proximit de
                    Montreuil sur mer, de la valle de la course, dtaples et
                    du Touquet paris-plage.
                  </p>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </section>
  );
};

export default FirstSection;
